.dropdown-menu {
    display: none;
}

@media screen and (max-width: 768px) {
    .sidebar-menu-button {
        cursor: pointer;
        margin: 10px;
        position: absolute;
        border: 1px solid white;
        border-radius: 0.3rem;
        height: 30px;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-align: center;
            align-items: center;
        padding: 0 13px 0 13px;
    }

    .menu-dot {
        width: 2px;
        height: 2px;
        border: 1px solid white;
        border-radius: 100%;
        background-color: white;
        margin: 0 2px 0 2px;
    }

    .dropdown-menu {
        height: 100vh;
        width: 100vw;
        display: block;
        background: -webkit-gradient(linear, left top, left bottom, color-stop(25%, rgba(0,0,0,0.8)),to(rgba(0,0,0,0.4)));
        background: -webkit-linear-gradient(top, rgba(0,0,0,0.8) 25%,rgba(0,0,0,0.4));
        background: -o-linear-gradient(top, rgba(0,0,0,0.8) 25%,rgba(0,0,0,0.4));
        background: linear-gradient(to bottom, rgba(0,0,0,0.8) 25%,rgba(0,0,0,0.4));
    }
}


